import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loading: false,
    auth: null,
    intervals: [],
  },
  getters: {
    authenticated: state => {
      return state.auth != null
    },
    auth: state => {
      return state.auth
    },
    loading: state => {
      return state.loading
    }
  },
  mutations: {
    setAuth(state) {
      state.auth = localStorage.getItem("AUTH_TOKEN")
    },
    unsetAuth(state) {
      state.auth = null
      localStorage.removeItem("AUTH_TOKEN")
    },
    loaded(state) {
      state.loading = false
    },
    loading(state, message) {
      message = message || null
      if (message == null) {
        state.loading = true
      } else {
        state.loading = message
      }
    },
    addInterval(state, interval) {
      state.intervals.push(interval)
    },
    flushIntervals(state) {
      for (let i in state.intervals) {
        window.clearInterval(state.intervals[i])
      }
    }

  },
  actions: {
    authenticate({ commit }) {
      commit("setAuth")
    },
    logout({ commit }) {
      commit("unsetAuth")
    },
    loaded({ commit }) {
      commit("loaded")
    },
    loading({ commit }, message) {
      commit("loading", message)
    },
    addInterval({ commit }, interval) {
      commit("addInterval", interval)
    },
    flushIntervals({ commit }) {
      commit("flushIntervals")
    }
  }
})
