<template>
  <div class="home container">
    <button class="btn btn-text float-right" @click="getData">
      <i class="fa fa-refresh" aria-hidden="true"></i>
    </button>

    <free-space :freespace="freespace"></free-space>
    <div class="text-center" v-if="freespace.length == 0">Caricamento in corso...</div>
    <hr />
    <h5>Monitoraggio</h5>
    <badges :lockedScripts="lockedScripts" :sdiData="sdiData" :sendingFE="sendingFE" :fobloccati="fobloccati"></badges>
    <hr />

    <h5>Fatturazione elettronica</h5>
    <div class="list-group">
      <router-link to="/fe/seigiorni" class="list-group-item list-group-item-action">
        <b>Fatture senza esito (2 gg):</b>
        {{ invoicesCount.noesito }}
      </router-link>
    </div>
    <hr />
    <h5>Script in esecuzione</h5>
    <div class="row">
      <div class="col-sm-12">
        <div class="list-group">
          <label>Processi monitorati</label>
          <div
            v-for="process in runningProcesses"
            :key="process.id"
            class="list-group-item list-group-item-action"
          >
            <b>{{ process.script_name }}</b>

            <div
              :title="process.path_script"
              style=" white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;"
            >
              <small>{{ process.path_script }}</small>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="text-center" v-if="runningProcesses.length == 0">Caricamento in corso...</div>
    <div class="row">
      <div class="col-sm-12">
        <div class="list-group">
          <label>Processi non monitorati</label>
          <div
            v-for="script in runningScripts"
            :key="script.id"
            class="list-group-item list-group-item-action"
          >
            <b>{{ script.name }}</b>
            da {{ script.since || '??' }} minuti
            <div
              :title="script.script"
              style=" white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;"
            >
              <small>{{ script.script }}</small>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="text-center" v-if="runningScripts.length == 0">Caricamento in corso...</div>
  </div>
</template>

<script>
import Vue from "vue";

/** DASHBOARD */
Vue.component("free-space", () => import("../components/Home/FreeSpace"));
Vue.component("badges", () => import("../components/Home/Badges"));

/** MODALS */
Vue.component("locked-modal", () =>
  import("../components/Home/modals/LockedModal")
);
Vue.component("errori-fo-modal", () =>
  import("../components/Home/modals/ErroriFOModal")
);
Vue.component("sending-fe-modal", () =>
  import("../components/Home/modals/SendingFEModal")
);
Vue.component("fo-bloccati-modal", () =>
  import("../components/Home/modals/FOBloccatiModal")
);

export default {
  name: "home",
  data() {
    return {
      extraURL:
        "https://archicons.archivium-srl.it/ArchiHub/procedures/tools/extra.php",
      freespace: [],
      runningScripts: [],
      invoicesCount: {
        noesito: "Calcolo in corso..."
      },
      lockedScripts: [],
      runningProcesses: new Array(),
      sendingFE: [],
      sdiData: { fo: { errors: 0 }, fi: { errors: 0 } },
      fobloccati: []
    };
  },
  mounted() {
    // this.$store.dispatch("loading");
    this.getData().then(() => {
      // this.$store.dispatch("loaded");

      this.$store.dispatch(
        "addInterval",
        window.setInterval(this.getFreeSpace, 3000)
      );
      this.$store.dispatch(
        "addInterval",
        window.setInterval(this.getRunningScripts, 5000)
      );
      this.$store.dispatch(
        "addInterval",
        window.setInterval(this.getRunningProcess, 5000)
      );

      this.$store.dispatch(
        "addInterval",
        window.setInterval(this.getExtra, 30000)
      );
    });
  },

  methods: {
    getData() {
      return new Promise(resolve => {
        this.getFreeSpace()
          .then(() => this.getRunningScripts())
          .then(() => this.getRunningProcess())
          .then(() => this.getExtra())
          .then(() => resolve());
        this.getInvoicesNoEsitoCount();
      });
    },
    getRunningProcess() {
      return new Promise(resolve => {
        this.axios.get(this.apiURL + "/monitor/running/details").then(r => {
          this.runningProcesses = r.data;
          resolve();
        });
      });
    },
    getFreeSpace() {
      return new Promise(resolve => {
        this.axios.get(this.apiURL + "/freespace?aree").then(r => {
          this.freespace = r.data;
          resolve();
        });
      });
    },
    getInvoicesNoEsitoCount() {
      return new Promise(resolve => {
        this.axios.get(this.apiURL + "/fe/no-esito/overview").then(r => {
          this.invoicesCount.noesito = r.data.Conteggio;
          resolve();
        });
      });
    },
    getRunningScripts() {
      return new Promise(resolve => {
        this.axios.get(this.apiURL + "/running-scripts").then(r => {
          this.runningScripts = r.data;
          resolve();
        });
      });
    },
    getExtra() {
      return new Promise(resolve => {
        var base =
          "https://archicons.archivium-srl.it/ArchiHub/procedures/tools/extra.php";
        this.axios
          .get(base + "?r=processes")
          .then(r => {
            this.lockedScripts = r.data;
          })
          .then(() => this.axios.get(base + "?r=fesend&simple"))
          .then(r => {
            this.sendingFE = r.data;
          })
          .then(() => this.axios.get(base + "?r=sdi"))
          .then(r => {
            this.sdiData = r.data;
          })
          .then(() => this.axios.get(base + "?r=fobloccati"))
          .then(r => {
            this.fobloccati = r.data;
            resolve();
          })
      });
    }
  }
};
</script>

<style lang="scss">
.progress-bar.bg-warning,
.progress-bar.bg-secondary {
  color: #555;
}

.progress-bar.bg-danger {
  color: white;
}

.serverspace {
  padding-bottom: 1.5rem;
}
</style>