import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'

import 'font-awesome/css/font-awesome.css'
import 'bootstrap/dist/css/bootstrap.css'

import 'bootstrap'

Vue.config.productionTip = false

// Vue.prototype.apiURL = `https://archicons.archivium-srl.it/ArchiHub/procedures/tools/api.php`;
Vue.prototype.apiURL = process.env.VUE_APP_APIURL
Vue.prototype.axios = axios

router.beforeEach((to, from, next) => {
  store.dispatch('loaded')
  store.dispatch('authenticate')
  store.dispatch('flushIntervals')

  if (to.meta.requiresAuth && !store.getters.authenticated) {
    next("/login")
  } else if (!to.meta.requiresAuth && store.getters.authenticated) {
    store.dispatch('logout')
    next("/login")
  } else {
    Vue.prototype.axios.defaults.headers.common['Authorization'] = store.getters.auth
    next()
  }
})

const Loader = Vue.component('loader', () => import("./components/Loader"))

new Vue({
  router,
  store,
  components: {
    Loader
  },
  render: h => h(App)
}).$mount('#app')
