import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    { path: "/", component: Home, meta: { requiresAuth: true } },
    { path: "/fe/seigiorni", component: () => import('./views/FatturaElettronica/ReportSeiGiorni.vue'), meta: { requiresAuth: true } },
    { path: "/fe/conteggi", component: () => import("./views/FatturaElettronica/Conteggi.vue"), meta: { requiresAuth: true } },
    { path: "/fe/wiit/quadrature", component: () => import('./views/FatturaElettronica/GruppoWiit/Quadrature.vue'), meta: { requiresAuth: true } },
    { path: "/admin/overview", component: () => import('./views/Admin/Overview.vue'), meta: { requiresAuth: true } },
    { path: "/login", component: () => import('./views/Login.vue'), meta: { requiresAuth: false } },
    { path: "/monitor", component: () => import('./views/Monitor/Processi.vue'), meta: { requiresAuth: true } },
    { path: "/ordini/conteggi", component: () => import('./views/Ordini/Conteggi.vue'), meta: { requiresAuth: true } },
    { path: "/ordini/upload", component: () => import('./views/Ordini/Upload.vue'), meta: { requiresAuth: true } },
    { path: "/conservazioni/lista", component: () => import('./views/Conservazioni/RetentionList.vue'), meta: { requiresAuth: true } },
    { path: "/conservazioni/scarto", component: () => import('./views/Conservazioni/MotivoScarto.vue'), meta: { requiresAuth: true } },
    { path: "/conservazioni/stati_coda", component: () => import('./views/Conservazioni/StatiCoda.vue'), meta: { requiresAuth: true } },
    { path: "/conservazioni/gbsoftware", component: () => import('./views/Conservazioni/Gbsoftware.vue'), meta: { requiresAuth: true } },
    { path: "/conservazioni/mysond", component: () => import('./views/Conservazioni/Mysond.vue'), meta: { requiresAuth: true } },
    { path: "/conservazioni/da_conservare_scorso_anno", component: () => import('./views/Conservazioni/DaConservareAnnoScorso.vue'), meta: { requiresAuth: true } },
    { path: "/conservazioni/lista_coda", component: () => import('./views/Conservazioni/ListaCoda.vue'), meta: { requiresAuth: true } },
    { path: "/conservazioni/report", component: () => import('./views/Conservazioni/Report.vue'), meta: { requiresAuth: true } },
    { path: "/conservazioni/sog_prod", component: () => import('./views/Conservazioni/AggSogProd.vue'), meta: { requiresAuth: true } },
    { path: "/varie/rendicontazione", component: () => import('./views/Varie/Rendicontazione.vue'), meta: { requiresAuth: true } },
    { path: "/varie/cron", component: () => import('./views/Varie/Schedulazione.vue'), meta: { requiresAuth: true } },
    { path: "/varie/query", component: () => import('./views/Varie/Query.vue'), meta: { requiresAuth: true } },
    { path: "/*", redirect: "/" },
  ]
})
export default router;
